a{
    background-color: transparent;
    text-decoration: none;
    cursor: pointer;
    color: #fff;
}
ul, menu, dir {
    display: block;
    list-style-type: disc;
    -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    -webkit-padding-start: 40px;
}
.menu-fixed{
    height: 70px;
}
.menu-fixed-container{
    left: 0;
    right: 0;
    top: 0;
    position: relative;
    z-index: 1;
}
.menu-fixed-main{
    height: 68px;
    background-color: transparent;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    padding: 0 4%;
    z-index: 2;
    font-size: 1.2rem;
    -webkit-transition: background-color .4s;
    -o-transition: background-color .4s;
    transition: background-color .4s;
}
.logo{
    text-decoration: none;
    font-size: 1.8em;
    color: #e50914;
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    margin-right: 5px;
    font-family: nf-icon;
    speak: none;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
}
.logo:before {
    content: url("logo-queima_Easy-Resize.com.jpg");
}

.menu-fixed-links{
    margin: 0;
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    height: 100%;
}
.menu-fixed-links-item{
    list-style-type: none;
    margin-left: 18px;
    height: 100%;
}
.menu-link{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    font-weight: 700;
    height: 100%;
}
.secondary-navigation {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    -ms-flex-positive: 1;
    flex-grow: 1;
    -ms-flex-pack: end;
    justify-content: flex-end;
    position: absolute;
    right: 4%;
    top: 0;
    height: 100%;
}
/* MovieCard */
.title-card {
    position: relative;
    margin: 0 2px;
    z-index: 1;
    cursor: pointer;
    outline: 0;
}
.sliderContent .slider-item {
    z-index: 1;
    display: inline-block;
    position: relative;
    white-space: normal;
    vertical-align: top;
}
.title-card {
    position: relative;
    margin: 0 2px;
    z-index: 1;
    cursor: pointer;
    outline: 0;
}
.title-card:not(.loadingTitle) {
    display: block;
}

.title-card .slider-refocus{
    outline: 0;
}
.artwork {
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 100% 100%;
    width: 100%;
    padding: 28.125% 0;
}
.title-card .video-preload-title {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #222;
    z-index: -1;
}
.title-card .video-preload-title .video-preload-title-label {
    color: #e5e5e5;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    padding: 1em;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
}




